.card {
  background: #fff;
  border-radius: 2px;
  position: relative;
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  width: 200px;
  height: 150px;
  margin: 15px 45px;
  border: 3px solid white;
}

.card > .img-container {
  height: 100%;
  overflow: hidden;
  object-fit: cover;
}

.card > .img-container > img {
  width: 100%; 
}

body {
  background-image: url("/ClickyGame/client/public/images/HP.jpg");
  background-size: cover;
}

.wrapper {
  padding-top: 50px;
  background: #ECEFF1;
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  padding: 20px;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-content: flex-start;
          align-content: flex-start;
  overflow: auto; 
  background-image: url(/HPClickyGame/static/media/navbar.f305df6c.jpg);
  background-size: cover;
}

